import React, { Component } from 'react';
import GoogleMapsReact from 'google-map-react';

import "./SpecificPosting.css";

const AnyReactComponent = ({ text }) => <div>{text}</div>;

class SpecificPosting extends Component {
    static defaultProps = {
        center: {
            lat: 39.1653,
            lon: 86.5264
        },
        zoom: 15
    };

    constructor(props) {
        super(props);
        this.state = {
        };
    }

  render() {
    return (
    <div class="primary-container">
            <header class="header">
                    <div class="navitemset">
                        <li class="logo"><img class="logo" src='logo.svg' alt="logo"></img></li>
                        <li class="title"><h1>Rentmate</h1></li>
                    </div>
                    <ul class="navitemset">
                            <li class="navitem"><h2>Listings</h2></li>
                            <li class="navitem"><h2>Login</h2></li>
                            <li class="navitem"><h2>My Dashbaord</h2></li>
                    </ul>
            </header>

            <main class="body">
                    <div class="specificposting-container">
                            <div class="aptinfo">
                                    <h3>APT INFO</h3>
                                    <hr />
                                    <h4>2540 S. Alley St. Bloomington, IN 47403</h4>
                                    <h4>$4,900</h4>
                                    <h4><u>Rooms</u>: 3 | <u>Bath</u>: 3.5</h4>
                                    <h4><u>Utilities</u></h4>
                                    <ul>
                                        <li>Gas: Included!</li>
                                        <li>Electric: (est.) $70</li>
                                        <li>Water: (est.) $100</li>
                                        <li>Internet: Included!</li>
                                    </ul>
                                    <h4><u>Amenities</u></h4>
                                    <ul>
                                        <li>Pet-Friendly</li>
                                        <li>Indoor Pool</li>
                                        <li>Free Money!</li>
                                    </ul>
                            </div>
                            <div class="contact">
                                    <h3>CONTACT</h3>
                                    <hr />
                            </div>
                            <div class="visitation">
                                    <h3>VISITATION</h3>
                                    <hr />
                            </div>
                            <div class="gallery">
                                    <h3>GALLERY</h3>
                                    <hr />
                             </div>
                            <div class="map">
                                    <h3>MAP</h3>
                                    <hr />
                                    <div style={{ height: '1fr', width: '1fr' }}>
                                        <GoogleMapsReact
                                        bootstrapURLKeys={{
                                            key: "153387415708-if58o9u5ms35717r21tvl543utp87366",
                                            language: 'us',
                                            region: 'us',
                                        }}
                                        defaultCenter={this.props.center}
                                        defaultZoom={this.props.zoom}
                                        >
                                        <AnyReactComponent
                                            lat={39.1653}
                                            lng={86.5264}
                                            text="My Marker"
                                        />
                                        </GoogleMapsReact>
                                    </div>
                            </div>
                            <div class="review">
                                    <h3>REVIEW</h3>
                                    <hr />
                            </div>
                    </div>
            </main>

            <div style={{ height: '100vh', width: '100vw' }}>
                                        <GoogleMapsReact
                                        bootstrapURLKeys={{
                                            key: "AIzaSyBSFD5npoXO1kSQvawK1NuAx197oSFv9pI",
                                            language: 'us',
                                            region: 'us',
                                        }}
                                        defaultCenter={this.props.center}
                                        defaultZoom={this.props.zoom}
                                        >
                                        <AnyReactComponent
                                            lat={39.1653}
                                            lng={86.5264}
                                            text="My Marker"
                                        />
                                        </GoogleMapsReact>
                                    </div>
            <footer class="footer"></footer>
    </div>
      );
  }
}

export default SpecificPosting;